import { TranslateService } from '@ngx-translate/core';
import { Component, ViewChild } from '@angular/core';
import { NavController, NavParams, ModalController, Slides } from 'ionic-angular';

@Component({
  selector: 'page-abcde-details',
  templateUrl: 'abcde-details.html',
})
export class AbcdeDetailsPage {
  
  title: string;
  language: string
  item = { content: [], summary: []};
  @ViewChild(Slides) slides: Slides;

  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    public translate: TranslateService,
    public modalCtrl: ModalController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AbcdePage');
  }

  ngOnInit() {
    this.item = this.navParams.get('item');
  }

  closeModal(){
    if(this.item.summary && this.slides.getActiveIndex()!== 0){
      this.slides.slideTo(0)
    } else {
      this.navCtrl.pop();
    }
  }

  goToSlide(index) {
    this.slides.slideTo(index, 1);
  }

  getActiveSlide(){
    return this.slides.getActiveIndex() === this.item.content.length ? this.item.content.length - 1 : this.slides.getActiveIndex();
  }
  
}



