import { Http } from '@angular/http';
import { TranslateService } from '@ngx-translate/core';
import { Component } from '@angular/core';
import { NavController, NavParams, ModalController } from 'ionic-angular';

import { AbcdeDetailsPage } from './abcde-details/abcde-details';

@Component({
  selector: 'page-abcde',
  templateUrl: 'abcde.html',
})
export class AbcdePage {

  title: string;
  items = [];
  language: string
  showReference: boolean
  
  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    private translate: TranslateService,
    public modalCtrl: ModalController,
    private http: Http) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AbcdePage');
  }

  ngOnInit() {
    console.log('ngOnInit AbcdePage', this.navParams);
    this.title = this.navParams.get('title');
    this.showReference = this.navParams.get('showReference');
    this.language = this.translate.currentLang;

    if (!this.language) {
      this.language = 'pt';
    }

    if (this.language != 'en' && this.language != 'pt' && this.language != 'es') {
      this.language = 'en';
    }

    this.http.get('assets/descriptions/' + this.language + '/'+this.navParams.get('name')+'.json').subscribe(data => {
      this.items = JSON.parse(data['_body']);
    });

  }

  openDetailsModal(item) {
    const profileModal = this.modalCtrl.create(AbcdeDetailsPage, { item: item });
    profileModal.present();
  }
  
}



