import { Component } from '@angular/core';
import { NavController, NavParams, ModalController } from 'ionic-angular';
import { Http } from '@angular/http';
import { TranslateService } from '@ngx-translate/core';


@Component({
  selector: 'page-quiz',
  templateUrl: 'quiz.html',
})
export class QuizPage {

  title: string;
  items = [];
  question: any = {};
  selectedAnswer: string;
  language: string
  index: number = 0;
  total: number = 0;
  correctAnswers: number = 0;
  wrongAnswers: number = 0;
  finished: boolean = false;
  
  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    private http: Http,
    private translate: TranslateService,
    public modalCtrl: ModalController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Quiz');
  }

  ngOnInit() {

      this.title = this.navParams.get('title');
      this.language = this.translate.currentLang;
  
      if (!this.language) {
        this.language = 'pt';
      }

      if (this.language != 'en' && this.language != 'pt' && this.language != 'es') {
        this.language = 'en';
      }
  
      this.getContent()

      this.translate.onLangChange.subscribe((event) => {
        this.getContent(event.lang)
      })
      
  }

  getContent(lang=this.language) {
    this.http.get('assets/descriptions/' + lang + '/'+this.navParams.get('name')+'.json').subscribe(data => {
      this.items = JSON.parse(data['_body']);
      this.question = this.items[0];
      this.total = this.items.length;
    });
  }

  nextQuestion() {
    if(this.items[this.index].answers.indexOf(this.selectedAnswer) !== -1) {
      ++this.correctAnswers;
    } else {
      ++this.wrongAnswers;
    }
    this.index++;
    this.question = this.items[this.index];
    this.selectedAnswer = null;
  }

  finishQuiz() {
    if(this.items[this.index].answers.indexOf(this.selectedAnswer) !== -1) {
      ++this.correctAnswers;
    } else {
      ++this.wrongAnswers;
    }
    this.finished = true;
  }

  resetQuiz() {
    this.selectedAnswer = null;
    this.correctAnswers = 0;
    this.wrongAnswers = 0;
    this.index = 0;
    this.finished = false;
    this.question = this.items[0];
  }
}



