import { AbcdeDetailsPage } from './../pages/abcde/abcde-details/abcde-details';
import { ToraxContentDetailsPage } from './../pages/torax-content/torax-content-details/torax-content-details';
import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { AppRate } from '@ionic-native/app-rate';

import { HttpClient, HttpClientModule } from '@angular/common/http';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';

import { HttpModule } from '@angular/http';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';

import { ToraxContentPage } from '../pages/torax-content/torax-content';

import { AbcdePage } from '../pages/abcde/abcde';
import { AbcdefghPage } from '../pages/abcdefgh/abcdefgh';
import { EclissatoPage } from '../pages/eclissato/eclissato';

import { QuizPage } from '../pages/quiz/quiz';

import { DescriptionPage } from '../pages/description/description';
import { AboutPage } from '../pages/about/about';
import { TermsPage } from '../pages/terms/terms';
import { DeveloperPage } from '../pages/developer/developer';

import { IonicImageViewerModule } from 'ionic-img-viewer';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import { SafariViewController } from '@ionic-native/safari-view-controller';

export function createTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    ToraxContentPage,
    AbcdePage,
    AbcdefghPage,
    EclissatoPage,
    DescriptionPage,
    AboutPage,
    TermsPage,
    DeveloperPage,
    ToraxContentDetailsPage,
    AbcdeDetailsPage,
    QuizPage
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    HttpModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (createTranslateLoader),
        deps: [HttpClient]
      }
    }),
    IonicModule.forRoot(MyApp, {
      platforms: {
        ios: {
          statusbarPadding: false,
          tabsHideOnSubPages: false
        }
      }
    }),
    IonicImageViewerModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    ToraxContentPage,
    AbcdePage,
    AbcdefghPage,
    EclissatoPage,
    DescriptionPage,
    AboutPage,
    TermsPage,
    DeveloperPage,
    ToraxContentDetailsPage,
    AbcdeDetailsPage,
    QuizPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    AppRate,
    InAppBrowser,
    SafariViewController,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



