import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

import { ToraxContentPage } from '../torax-content/torax-content';

import { AbcdePage } from '../abcde/abcde';
import { QuizPage } from '../quiz/quiz';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  mainPages: Array<{title: string, name:string, icon?: string, color?: string, component: any, showReference?: boolean}>;
  methodsPages: Array<{title: string, name: string, icon?: string, color?: string, component: any, showReference?: boolean}>;
  otherPages: Array<{title: string, name: string, icon?: string, color?: string, component: any}>;
  
  constructor(public navCtrl: NavController) {
    this.mainPages = [
      { title: 'TITLE_ANATOMY_TORAX', name: 'anatomy-torax', color: 'primary', component: ToraxContentPage, showReference: true },
      { title: 'TITLE_ANATOMY_RADIOLOGY', name: 'anatomy-radiological', color: 'primary', component: ToraxContentPage, showReference: true },
      { title: 'TITLE_POSITIONS', name: 'positions-radiological', color: 'primary', component: ToraxContentPage, showReference: true },
      { title: 'TITLE_TOMOGRAFY', name:'tomografy', component: ToraxContentPage, showReference: true },
      { title: 'TITLE_PATHOLOGIES_FINDINGS', name:'pathologies-findings', component: ToraxContentPage, showReference: true },
      { title: 'TITLE_STANDARD_REPORT', name:'standard-report', component: ToraxContentPage, showReference: true },
    ];

    this.methodsPages = [
      //{ title: 'TITLE_ABCDE', name: 'abcde-method', color: 'secondary', component: AbcdePage },
      { title: 'TITLE_ABCDEFGH', name: 'abcdefgh-method', color: 'secondary', component: AbcdePage, showReference: true },
      { title: 'TITLE_ECLISSATO', name: 'ecclissato-method', color: 'secondary', component: AbcdePage },
    ];

    this.otherPages = [
      { title: 'QUIZ_1', name: 'quiz', color: 'tertiary', component: QuizPage },
      { title: 'QUIZ_2', name: 'quiz-2', color: 'tertiary', component: QuizPage },
      { title: 'QUIZ_3', name: 'quiz-3', color: 'tertiary', component: QuizPage },
    ];
  }

  openPage(page) {
    this.navCtrl.setRoot(page.component, { name: page.name, title: page.title, showReference: page.showReference });
  }
}



