import { Component, ViewChild } from '@angular/core';
import { Slides, NavController, NavParams } from 'ionic-angular';
import { Http } from '@angular/http';
import { ModalController } from 'ionic-angular/components/modal/modal-controller';

/**
 * Generated class for the AnatomyToraxDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-torax-content-details',
  templateUrl: 'torax-content-details.html',
})
export class ToraxContentDetailsPage {

  item = { content: [], summary: []};
  @ViewChild(Slides) slides: Slides;

  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    private http: Http,
    public modalCtrl: ModalController) {
      this.item = this.navParams.get('item');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ToraxContentDetailsPage');
  }

  closeModal(){
    if(this.item.summary && this.slides.getActiveIndex()!== 0){
      this.slides.slideTo(0)
    } else {
      this.navCtrl.pop();
    }
  }

  goToSlide(index) {
    this.slides.slideTo(index, 1);
  }

  getActiveSlide(){
    return this.slides.getActiveIndex() === this.item.content.length ? this.item.content.length - 1 : this.slides.getActiveIndex();
  }

  openLinkedTo(linkedTo) {
    console.log('openLinkedTo, linkedTo:', linkedTo);
    if(linkedTo){
      this.http.get(linkedTo.url).subscribe(data => {
        var jsonResponse = JSON.parse(data['_body']);
        for(let item of jsonResponse){
          if(item.id == linkedTo.id){
            const profileModal = this.modalCtrl.create(ToraxContentDetailsPage, { item: item });
            profileModal.present();
          }
        }
      });
    }
  }

}



