import { ToraxContentDetailsPage } from './torax-content-details/torax-content-details';
import { Component } from '@angular/core';
import { NavController, NavParams, ModalController } from 'ionic-angular';
import { Http } from '@angular/http';
import { TranslateService } from '@ngx-translate/core';
import { Platform } from 'ionic-angular';

@Component({
  selector: 'page-torax-content',
  templateUrl: 'torax-content.html',
})
export class ToraxContentPage {

  title: string;
  showReference: boolean;
  items = [];
  language: string
  
  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    private http: Http,
    private translate: TranslateService,
    public modalCtrl: ModalController,
    public platform: Platform) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ToraxContent');
  }

  ngOnInit() {

      console.log('ngOnInit this.navParams', this.navParams);
      this.title = this.navParams.get('title');
      this.showReference = this.navParams.get('showReference');
      this.language = this.translate.currentLang;
  
      if (!this.language) {
        this.language = 'pt';
      }

      if (this.language != 'en' && this.language != 'pt' && this.language != 'es') {
        this.language = 'en';
      }
  
      this.getContent()

      this.translate.onLangChange.subscribe((event) => {
        this.getContent(event.lang)
      })
  }

  getContent(lang=this.language) {
    this.http.get('assets/descriptions/' + lang + '/'+this.navParams.get('name')+'.json').subscribe(data => {
      this.items = JSON.parse(data['_body']);
      if (this.platform.is('ios')) {
        for (var i=0;i<this.items.length;i++) {
          if (this.items[i].id == 'covid') {
            this.items.splice(i, 1);
          }
        }
      }
    });
  }

  openDetailsModal(item) {
    const profileModal = this.modalCtrl.create(ToraxContentDetailsPage, { item: item });
    profileModal.present();
  }
}



